import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Radio from "@mui/material/Radio";
import FormControlLabel from "@mui/material/FormControlLabel";

const dispatchedEvents = [];

const SrsMuiRadio = function (props, ref) {
  return (
    <FormControlLabel value={props.radiovalue} control={<Radio {...props} theme={props.muiTheme}/>} label={props.label} theme={props.muiTheme}/>
  );
};

const styles = `
  /* Custom styles for the MuiRadio component */
`;

const props = [
  {
    name: "label",
    defaultValue: "",
    type: "string",
  },
  {
    name: "checked",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "value",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Radio if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiRadio),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
